import {
	mapState,
	mapActions,
	mapGetters,
	mapMutations
} from 'vuex'
import {
	uniPopup,
	uniBadge,
	uniList,
	uniListItem,
} from "@dcloudio/uni-ui"

export default {

	onLoad() {
		if (this.isLogin) {}
	},

	computed: {
		...mapState('authed', [
			'isLogin',
			'userInfo',
		]),
		...mapState('home', [
			'individualPoints',
		]),

	},

	components: {
		uniPopup,
		uniBadge,
		uniList,
		uniListItem,

	},

	methods: {
		...mapActions('message', [
			'getMessageListAction',
		]),

	}
}
